// import vector from '../assets/images/cancel-orange.svg'
import centerMarker from '../assets/images/map-center.svg'
import selfMarker from '../assets/images/marker-self.svg'
import targetMarker from '../assets/images/marker-target2.svg'

import React, { useMemo, useCallback, useRef, useState, useEffect } from "react"
import { GoogleMap, Marker, DirectionsRenderer } from "@react-google-maps/api"

import ParkingMark from "./ParkingMark"



// currentPosition
const getUserPos = (setSelfPos, mode, setMapCenter) => {
  console.log("getUserPos");
  if (navigator.geolocation) {
    navigator.geolocation.watchPosition(
      (position) => {
        if (setSelfPos) {
          setSelfPos(() => {
            console.log("setSelfPos");
            return {
              lat: position.coords.latitude,
              lng: position.coords.longitude,
            }
          })
          if(mode !== 'self') return
          if(!position) return
          setMapCenter(() => {
            console.log("setMapCenter");
            return {
              lat: position.coords.latitude,
              lng: position.coords.longitude,
            };
          });
        }
      },
      (error) => {
        console.log('', error)
      }
    );
  } else {
    //
    alert("");
  }
}

// currentPosition
const watchUserPos = (setSelfPos) => {
  console.log('watchUserPos')
  if (navigator.geolocation) {
    navigator.geolocation.watchPosition(
      (position) => {
        if (setSelfPos) {
          setSelfPos(() => {
            console.log("setSelfPos");
            return {
              lat: position.coords.latitude,
              lng: position.coords.longitude,
            }
          })
        }
      },
      (error) => {
        console.log('', error)
      }
    );
  } else {
    //
    alert("");
  }
}

//
const handleFetchDirections = (origin, destination, state, setter) => {
  console.log('')
  //
  if (state) {
    setter(null)
  }
  if (!origin || !destination) return console.log("");
  
  const google = window.google;

  //api
  const service = new google.maps.DirectionsService();
  //DirectionsService.route
  service.route(
    {
      origin,
      destination,
      travelMode: google.maps.TravelMode.DRIVING,
    },
    (result, status) => {
      if (status === "OK" && result) {
        setter(result)
      }
    }
  )
}


export default function Map({mapCenter, setMapCenter, mode, mapInstance, setMapInstance, target, setTarget, setSpeech, setSelfPos, directions, setDirections, transOption, mapRef, selfPos, currentPark, setCurrentPark,  canFetchDirection, setCanFetchDirection, remainings, setRemainings, isFollow, setIsFollow}) {


  // currentPosition
  useEffect(() => {
    console.log('Map load')
    getUserPos(setSelfPos, mode, setMapCenter)
    watchUserPos(setSelfPos)
  }, []);

  //selfPos
  useEffect(() => {
    if (!isFollow) return
    if (!selfPos) return
    if (!mapInstance) return
    if (isFollow) {
      if (!mapInstance.map) {
        return mapInstance.setCenter(selfPos)
      }
      mapInstance.map.setCenter(selfPos)
    }
  }, [selfPos, isFollow, mapInstance])

  // center 
  function handleCenterChanged() {
    if (mode !== "screen-center") return
    
    // console.log('mapInstance', mapInstance)
    
    if (mapInstance.map) {
      setMapCenter(mapInstance.map.center.toJSON())
    } else {
      setMapCenter(mapInstance.center.toJSON())
    }
  }

  // map  mapRef useCallback: 
  const onLoad = useCallback((map) => {
    console.log('')
    if (map.map) {
      return setMapInstance(map.map)
    }
    mapRef.current = map
    return setMapInstance(map)
  }, [])

  // mode 
  useEffect(() => {
    if (mode === 'self') {
      console.log('current mode: ', mode)
      if (!isFollow) {
        //modeself
        setIsFollow(true)
      }
      if (!selfPos) return
      setMapCenter(selfPos)
      setTarget(null)
      setSpeech('')
      return
    }
    if (mode === 'target') {
      console.log('current mode: ', mode)
      return
    }
    if (mode === 'screen-center') { //
      setDirections(null)
      console.log('current mode: ', mode)

      setMapCenter(selfPos)
      setTarget(null)
      setSpeech('')
      return
    }
  }, [mode])

  // or (marker) or  = 
  useEffect(() => {
    if (!canFetchDirection) return console.log('canfetchDirection false')
    if (!currentPark) return console.log('')
    const positon = {lng: currentPark.lng, lat: currentPark.lat}
    // handleFetchDirections(selfPos, positon , directions, setDirections)
    //false
    setCanFetchDirection(false)
  }, [canFetchDirection])



  //map
  const options = useMemo(
    () => ({
      //google map
      mapId: "feb728f5023695e4",
      //UI
      disableDefaultUI: true,
      //
      clickableIcons: false,
      //
      gestureHandling: 'greedy'
    }),
    []
  )

  return(
    <div className="map">
      <GoogleMap
        zoom={15}
        center={mapCenter}
        mapContainerClassName="map"
        onDragEnd={handleCenterChanged}
        onDragStart={() => {
          if (!isFollow) return
          setIsFollow(false)
        }}
        onZoomChanged={() => {
          if (!isFollow) return
          setIsFollow(false)
        }}
        options={options}
        onLoad={onLoad}
      >
        {directions && (
          <DirectionsRenderer
            directions={directions}
            options={{
              polylineOptions: {
                zIndex: 10,
                strokeColor: "#33CC99",
                strokeWeight: 7,
              },
            }}
          />
        )}
        {selfPos && (
          <Marker 
            position={selfPos} 
            className="self-point marker" 
            icon={{
              url: selfMarker,
              scaledSize: { width: 32, height: 32 },
              className: 'marker'
            }}
            />
        )}
        {mapCenter && mode === "screen-center" && (
          <Marker
            className="marker"
            onLoad={onLoad}
            position={mapCenter}
            icon={{
              url: centerMarker,
              scaledSize: { width: 28, height: 28 },
              className: 'marker'
            }}
            zIndex={999}
          />
        )}
        {target && <Marker 
          position={target} 
          zIndex={998}
          icon={{
            url: targetMarker,
            scaledSize: { width: 48, height: 48 },
            className: 'marker'
          }}
          />}
        <ParkingMark 
          mode={mode}
          transOption={transOption}
          mapCenter={mapCenter}
          target={target}
          selfPos={selfPos}
          handleFetchDirections={handleFetchDirections}
          directions={directions}
          setDirections={setDirections}
          currentPark={currentPark}
          setCurrentPark={setCurrentPark}
          setCanFetchDirection={setCanFetchDirection}
          remainings={remainings}
          setRemainings={setRemainings}
        />
      </GoogleMap>
    </div>
  )

}
